import axios from "axios"

const TODOS_API = "http://localhost:1212/todos"

class UserServices {
  getTodo = () => {
    return axios.get(TODOS_API, {
      headers: {
        'Access-Control-Allow-Origin' : '*',
        'Access-Control-Allow-Methods' : 'GET,PUT,POST,DELETE,PATCH,OPTIONS',
      }
    })
  }

  postTodo = (todo) => {
    return axios.post("http://localhost:1212/todo", {
      "id": todo.id,
      "task": todo.task,
      "status": todo.status
    })
  }
}

export default new UserServices