import React from 'react'
import UserServices from '../service/UserServices'

class TodoBar extends React.Component {
  constructor(props, context) {
    super(props, context)
    this.state = {
      id: 111111,
      task: "",
      status: 0,
    }
  }

  onChange = (event) => {
    this.setState({
      task: event.target.value
    })
    event.preventDefault()
  }

  onClick = () => {
    if (this.state.task.length>0) {
      let todo = { id: this.state.id, todo: this.state.task, status: this.state.status }
      console.log("what will be send");
      console.log(todo);
      UserServices.postTodo(todo)
      // .then(res => {
      //   console.log(res.status);
      // })
    }
    this.setState({
      task: ""
    })
  }

  render() {
    return (
      <div>
        <input type="text" value={this.state.todo} onChange={this.onChange} />
        <button onClick={this.onClick}>Add</button>
      </div>
    )
  }
}

export default TodoBar